import { useMemo } from 'react';

import {
	formatMoney as formatMoneyAmount,
	localizeMoney as localizeMoneyAmount,
	parseMoney,
	parseLocalizedMoney as parseLocalizedMoneyAmount,
} from '@eventespresso/utils';
import { useConfig } from '../config';
import type { FormatMoneyFunction, ParseMoneyFunction } from '@eventespresso/utils';
import type { Type as ConfigType } from '@eventespresso/config';

export interface MoneyDisplay {
	/** the currency sign if the currency displays it before the amount (or '') */
	afterAmount: string;
	/** the currency sign if the currency displays it before the amount (or '') */
	beforeAmount: string;
	/** the full currency config object */
	currency: ConfigType.Currency;
	/** functions for formatting the amount using the correct number of decimal places for the currency */
	formatMoney: FormatMoneyFunction;
	localizeMoney: FormatMoneyFunction;
	/** functions for unformatting the amount using the correct number of decimal places for the currency */
	parseMoney: ParseMoneyFunction;
	parseLocalizedMoney: ParseMoneyFunction;
}

export const useMoneyDisplay = (): MoneyDisplay => {
	const { currency, locale } = useConfig();

	const afterAmount = currency.signB4 ? '' : currency.sign;
	const beforeAmount = currency.signB4 ? currency.sign : '';

	const formatMoney = formatMoneyAmount(currency.decimalPlaces);
	const localizeMoney = localizeMoneyAmount(currency.decimalPlaces, locale.site);
	const parseLocalizedMoney = parseLocalizedMoneyAmount(currency.decimalMark, currency.thousandsSeparator);

	return useMemo(
		() => ({
			afterAmount,
			beforeAmount,
			currency,
			formatMoney,
			localizeMoney,
			parseMoney: parseMoney,
			parseLocalizedMoney: parseLocalizedMoney,
		}),
		[afterAmount, beforeAmount, currency, localizeMoney, formatMoney, parseLocalizedMoney]
	);
};

export default useMoneyDisplay;
